.game-img {
  background: transparent;
  height: 200px;
  margin: 10px;
  position: relative;
  width: 200px;
 
}

.game-img > img {
object-fit: contain;
height: 175px;
width: 175px;
}
.wrapper {
  padding-top: 50px;
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/clicky-game/static/media/road.c2c3ee06.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/clicky-game/static/media/road.c2c3ee06.jpg);
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  width: 70%;
  height:70%;
  margin: auto;
  background-position: center;
  background-repeat: no-repeat; 
  background-size: cover;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

